import { cancelBubble } from '@cetus/utils'
import { Flex, HTMLChakraProps, Text, TextProps } from '@chakra-ui/react'
import { useNavigate } from 'react-router-dom'
import Icon from './Icon'

interface BackProps extends HTMLChakraProps<'div'> {
  text?: string
  customTextStyle?: TextProps // 避免命名冲突
  svgFill?: string
  onClick?: () => void
}

export default function BackButton({ text, onClick, svgFill = 'text_paragraph', customTextStyle, ...rest }: BackProps) {
  const navigate = useNavigate()
  return (
    <Flex
      w="73px"
      h="32px"
      align="center"
      justifyContent="center"
      bg="bg_secondary"
      borderRadius="8px"
      border="1px solid"
      borderColor="border"
      cursor="pointer"
      fontWeight="500"
      onClick={e => {
        cancelBubble(e)
        if (onClick) {
          onClick()
        } else {
          navigate(-1)
        }
      }}
      sx={{
        _hover: {
          p: {
            color: 'text_caption'
          },
          svg: {
            fill: 'text_caption'
          }
        }
      }}
      {...rest}
    >
      {text !== 'Back to DEX' && <Icon ml="-6px" xlinkHref="#icon-icon_descending_nor" svgFill={svgFill} fontSize="20px" transform="rotate(90deg)" />}
      <Text lineHeight="1" {...customTextStyle}>
        {text ? text : 'Back'}
      </Text>
    </Flex>
  )
}
